.card {
  margin-right: 30px;
  margin-bottom: 10px;
  position: relative;
  width: 100%;
  height: 300px;
  overflow: hidden;

  .backgroundImage {
    position: absolute;
    width: 100%;
    height: 300px;
    overflow: hidden !important;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    transition: 0.5s ease;
    border-radius: 5px;
  }

  .hoverBlur {
    filter: blur(4px);
    transition: 0.5s ease;
    // -webkit-backdrop-filter: blur(14px);
    // backdrop-filter: blur(14px);
  }

  .content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: "Open Sans";
    text-align: center;
    padding: 10px;
    opacity: 0;
    transition: all 500ms ease-in-out;

    border-radius: 5px;

    // -webkit-backdrop-filter: blur(9px);
    cursor: pointer;
    h1 {
      font-size: 20px;
      font-weight: 700;
      color: white;
    }
    p {
      font-size: 15px;
      font-weight: 500;
      color: white;
      margin-bottom: 20px;
    }

    .btn {
      background-color: black;
      color: white;
      padding: 10px;
      text-decoration: none;
      border: none;
      cursor: pointer;
    }
    &:hover {
      opacity: 1;
      background: rgba(0, 0, 0, 0.15);
      transform: translate(0, 0px);
    }
  }
}
